@import url(https://fonts.googleapis.com/css?family=Mukta:400,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
@import url(https://use.fontawesome.com/releases/v5.6.3/css/all.css);
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
  line-height: 1;
  font-family: 'Mukta', sans-serif !important;
	-webkit-font-smoothing: antialiased;
	background-color: #fafafa;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.hero-title {
  font-size: 42px;
  color: white;
  text-align: center;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.12);
}

/* Content */
.main-content {
  box-sizing: border-box;
  padding: 40px 0;
}
.content {
  /* display: block; */
  /* margin: 0 auto; */
  /* background: yellow; */
  /* width: 600px; */
  /* text-align: center; */
}
.content h2 {
  position: relative;
  color: #131313;
  font-size: 26px;
  font-weight: bold;
  line-height: 1.5;
  /* opacity: .7; */
}

/* Splash page */
.splash-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-image: -webkit-linear-gradient(315deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%);
  background-image: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, #FF719A 100%);
}
.splash-page h1 {
  font-size: 54px;
  font-weight: bold;
}

/* List Surah */
.list-surah {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: salmon; */
  padding: 20px 20px;
  box-sizing: border-box;
  margin: 3px 0;
  text-decoration: none !important;
  border-radius: 3px;
  text-align: left;
}
.list-surah p {
  color: #FF719A;
  font-weight: bold;
}
.list-surah h3 {
  color: #FF719A;
  font-size: 18px;
  font-weight: bold;
}
.list-surah:hover {
  color: #fff !important;
  background-color: #f2f4fb;
  /* background-image: linear-gradient(-225deg, #FFE29F 0%, #FFA99F 48%, # 100%); */
}

/* Footer */
footer {
  padding: 50px;
  box-sizing: border-box;
  text-align: center;
}
footer code {
  font-size: 12px;
  font-family: 'Roboto Mono', Courier, monospace;
}

/* Detail Surat */
.detail-surat p {
  display: block;
  margin: 0 auto;
  color: white;
  font-weight: 300;
  text-align: center;
  line-height: 1.5;
  opacity: .8;
  margin: 20px 0 30px;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.12);
}

.notfound-page {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  /* background-image: url('./assets/ilus.png'); */
}
